// @ts-nocheck
import Vue from 'vue';

import App from './App.vue';
import router from './router';
import components from './plugin/components.js';
import Origin from './plugin/Origin.js';
import url from './plugin/url.js';
import filter from './plugin/filter.js';
import ajax from './plugin/ajax.js';


import $ from 'jquery';


import 'vant/lib/vant-css/icon-local.css';
import '@/style/style.scss';
import '@/style/theme/Vant.scss';
import 'font-awesome-webpack'
import store from '@/store'

import {
    SubmitBar,
    Tab,
    Tabs,
    NavBar,
    Icon,
    Search,
    Swipe,
    SwipeItem,
    PullRefresh,
    List,
    Toast,
    Button,
    Popup,
    Checkbox,
    CheckboxGroup,
    Row,
    Col,
    Cell,
    Card,
    CellGroup,
    Badge,
    Field,
    Loading,
    Lazyload,
    Radio,
    Tag,
    Dialog,
    ContactCard,
    ContactList,
    ContactEdit,
    RadioGroup,
    Panel,
    Area,
    PasswordInput,
    NumberKeyboard,
    Step,
    Steps,
    Stepper,
    NoticeBar,
    Progress,
    Tabbar, 
    TabbarItem,
    SwipeCell,
} from "vant";

Vue.use(Tab)
    .use(Tabs)
    .use(NavBar)
    .use(Icon)
    .use(Search)
    .use(Swipe)
    .use(SwipeItem)
    .use(PullRefresh)
    .use(List)
    .use(Toast)
    .use(Button)
    .use(Popup)
    .use(Checkbox)
    .use(CheckboxGroup)
    .use(Row)
    .use(Col)
    .use(Cell)
    .use(CellGroup)
    .use(Badge)
    .use(Field)
    .use(Loading)
    .use(Lazyload)
    .use(Radio)
    .use(Tag)
    .use(Dialog)
    .use(ContactCard)
    .use(ContactList)
    .use(ContactEdit)
    .use(RadioGroup)
    .use(Panel)
    .use(Area)
    .use(PasswordInput)
    .use(NumberKeyboard)
    .use(Step)
    .use(Steps)
    .use(Stepper)
    .use(NoticeBar)
    .use(Progress)
    .use(Card)
    .use(SubmitBar)
    .use(Tabbar)
    .use(TabbarItem)
    .use(SwipeCell)


//=======================================================================
//官方插件


// const store = new Vuex.Store({
//     state: {
//         count: 0,
//     },
//     mutations: {
//         setShow(state, fun) {
//             state.showShare = fun;
//         },
//     }
// })

//=======================================================================
//自定义插件
import Directive from './plugin/directive.js';
Vue.use(Directive);
Vue.use(url);//url处理
Vue.use(components);//自己的组件列表
Vue.use(ajax);
Vue.use(Origin);
Vue.use(filter);



//=======================================================================
//第三方插件
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
Vue.use(VueAwesomeSwiper);

import PullTo from 'vue-pull-to';
Vue.component('pull-to', PullTo);
//=======================================================================


Vue.config.productionTip = true;


//消除手机端点击延迟
// const FastClick = require('fastclick');
// FastClick.attach(document.body);

/* eslint-disable no-new */


new Vue({
    el: '#App',
    router,
    store,
    render: h => h(App),
    mounted() {

    }
})





