export default {
	setShow(state, fun) {
		state.showShare = fun;
	},
	changetitle(state,val){
		state.goodstitle=val;
	},
	cartfilter(state,val){
		for(let i=0;i<state.ShoppingCart.length;i++){
			if(state.ShoppingCart[i].goodschashu.GoodsTitle==val.goodschashu.GoodsTitle){				
				state.ShoppingCart[i].goodsNumber++;
				return;
			}
		}
		state.ShoppingCart.push(val)
	},
	updateShopping(state,val){
		let newval;
		for(let i=0; i<val.length;i++){
			newval=state.ShoppingCart.findIndex((age)=>age.goodschashu.GoodsTitle==val[i]);
            state.ShoppingCart.splice(newval,1)
		}	


	}
}


