// @ts-nocheck
import Vue from 'vue';
import Router from 'vue-router';

// const Login = () => import('@/pages/login/login.vue')
const home = () => import('@/pages/home/home.vue');
const Goods = () => import('@/pages/commodity/goods.vue');
const purchase = () => import('@/pages/purchase/purchase.vue');
const shoppingcart = () => import('@/pages/shoppingcart/shoppingcart.vue');
shoppingcart
Vue.use(Router);

export default new Router({
  // mode: 'history',
  scrollBehavior(to, from, savedPosition) { },
  routes: [
    // {
    //   path: '/GoodsGroup',
    //   component: GoodsGroup,
    //   meta: {
    //     // index: 2048,
    //     //keepAlive: true
    //   }
    // },
    {
      path: '/',
      component: home,
      children:[
      {
        path: '/',
        component: Goods,
      },
      {
        path: '/shoppingcart',
        component: shoppingcart,
      },
      ]
    },
    
    { 
      path: '/purchase',
      component: purchase,
    }
    ]
  })



